@use 'functions' as *;

/* Spacing */

$spacing-4: 4px;
$spacing-8: 8px;
$spacing-12: 12px;
$spacing-16: 16px;
$spacing-24: 24px;
$spacing-32: 32px;
$spacing-48: 48px;
$spacing-64: 64px;
$spacing-96: 96px;
$spacing-128: 128px;
$spacing-192: 192px;
$spacing-256: 256px;

/* Sizes */

$sidebar-resizer-width: $spacing-12;
$sidebar-toggle-button-width: $spacing-12 + 2px;
$sidebar-toggle-button-height: $spacing-48;

$nav-window-nav-bar-width: 250px;

$modal-width: 650px;
$modal-width-narrow: 400px;

$menu-max-height: 300px;

$fft-height: 100px;

/* Depth */

$modal-z-index: 1000;
$overlay-z-index: 950;
$context-menu-z-index: 600;
$menu-z-index: 500;
$alert-z-index: 30;
$toolbar-z-index: 20;
$sidebar-toggle-button-z-index: 11;
$sidebar-resizer-z-index: 10;

/* Exports */

:export {
  SIDEBAR_RESIZER_WIDTH: strip-unit($sidebar-resizer-width);
}
