@use 'border' as *;
@use 'colors' as *;
@use 'shadows' as *;

@mixin inset-section {
  @include inset-box-shadow-3($v-offset: $border-depth-size);
  border-bottom: $border-depth-size solid $inset-border;
  background: $inset-background;
}

@mixin inset-section-hover {
  border-color: $inset-border-hover;
  background: $inset-background-hover;
}

@mixin scrollbar-light {
  &::-webkit-scrollbar-thumb {
    background: $background-scrollbar-light;
  }
}

@mixin scrollbar-lightest {
  &::-webkit-scrollbar-thumb {
    background: $background-scrollbar-lightest;
  }
}
