@use 'colors' as *;

/* Shadows */

@mixin box-shadow-0 {
  box-shadow: none;
}

@mixin box-shadow-1 {
  box-shadow:
    0 1px 2px $shadow-010,
    0 1px 2px $shadow-040;
}

@mixin box-shadow-2 {
  box-shadow:
    0 3px 3px $shadow-020,
    0 2px 2px $shadow-040;
}

@mixin box-shadow-3 {
  box-shadow:
    0 7px 5px $shadow-020,
    0 3px 3px $shadow-030;
}

@mixin box-shadow-4 {
  box-shadow:
    0 10px 8px $shadow-030,
    0 4px 4px $shadow-020;
}

@mixin box-shadow-5 {
  box-shadow:
    0 15px 13px $shadow-040,
    0 5px 5px $shadow-010;
}

/* Inset shadows */

@mixin inset-box-shadow-0 {
  box-shadow: none;
}

@mixin inset-box-shadow-1($v-offset: 0) {
  box-shadow: inset 0 $v-offset 2px 1px $shadow-010;
}

@mixin inset-box-shadow-2($v-offset: 0) {
  box-shadow: inset 0 $v-offset 3px 1px $shadow-020;
}

@mixin inset-box-shadow-3($v-offset: 0) {
  box-shadow: inset 0 $v-offset 4px 1px $shadow-030;
}

@mixin inset-box-shadow-4($v-offset: 0) {
  box-shadow: inset 0 $v-offset 5px 2px $shadow-040;
}

@mixin inset-box-shadow-5($v-offset: 0) {
  box-shadow: inset 0 $v-offset 6px 2px $shadow-050;
}
